type CacheEntry<T> = { data: T; timestamp: number };

const memoryCache = new Map<string, CacheEntry<any>>();

export function getCached<T>(key: string): CacheEntry<T> | undefined {
  const mem = memoryCache.get(key);
  if (mem) return mem as CacheEntry<T>;
  // Fallback to localStorage if available
  if (typeof window !== 'undefined' && window.localStorage) {
    try {
      const raw = window.localStorage.getItem(`admin-cache:${key}`);
      if (!raw) return undefined;
      const parsed = JSON.parse(raw) as CacheEntry<T>;
      return parsed;
    } catch {
      return undefined;
    }
  }
  return undefined;
}

export function setCached<T>(key: string, data: T): void {
  const entry = { data, timestamp: Date.now() } as CacheEntry<T>;
  memoryCache.set(key, entry);
  if (typeof window !== 'undefined' && window.localStorage) {
    try {
      window.localStorage.setItem(`admin-cache:${key}`, JSON.stringify(entry));
    } catch {}
  }
}

export function getCacheKey(parts: Array<string | number | boolean | null | undefined>): string {
  return parts.filter((p) => p !== undefined && p !== null).join(":");
}

export function isFresh(entry: CacheEntry<any> | undefined, ttlMs: number): boolean {
  if (!entry) return false;
  return Date.now() - entry.timestamp < ttlMs;
}

export function deleteCached(key: string): void {
  memoryCache.delete(key);
  if (typeof window !== 'undefined' && window.localStorage) {
    try { window.localStorage.removeItem(`admin-cache:${key}`); } catch {}
  }
}

// Version helpers for version-first loading
export function getVersion(key: string): string | undefined {
  if (typeof window === 'undefined' || !window.localStorage) return undefined;
  try { return window.localStorage.getItem(`admin-version:${key}`) || undefined; } catch { return undefined; }
}

export function setVersion(key: string, version: string): void {
  if (typeof window === 'undefined' || !window.localStorage) return;
  try { window.localStorage.setItem(`admin-version:${key}`, version); } catch {}
}


